import axios from 'axios';
import { SetMessage } from '../redux/actions/messageActions';
import { store } from '../redux/store';
import AuthService from '../services/AuthService';

export const BASE_URL = 'https://ualib-orion.herokuapp.com/api/v1';

const $api = axios.create({
	baseURL: BASE_URL,
	// Accept: application/json, text/plain, */*
// Accept-Encoding: gzip, deflate, br
// Accept-Language: ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7,uk;q=0.6
// Access-Control-Allow-Credentials: true
// Access-Control-Allow-Headers: Origin, X-Api-Key, X-Requested-With, Content-Type, Accept, Authorization
// Access-Control-Allow-Methods: POST, PUT, PATCH, GET, DELETE, OPTIONS
// Access-Control-Allow-Origin: http://localhost:3000
// authorization: Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0b2tlbl90eXBlIjoiYWNjZXNzIiwiZXhwIjoxNjc1NDUyODA3LCJpYXQiOjE2NzU0NDAwNzYsImp0aSI6ImFiMWM2Y2JlYTA2NDQ1NjBhYWMzNDc2NDhjYTZlYzk5IiwidXNlcl9pZCI6MiwiaXNfc3RhZmYiOnRydWV9.oWkwnaXaiGvC6qlgKNyL3O4rpriN2pnWCO_F8yt6sPg
// Connection: keep-alive
// Host: ualib-orion.herokuapp.com
// Origin: http://localhost:3000
// Referer: http://localhost:3000/
// sec-ch-ua: "Not_A Brand";v="99", "Google Chrome";v="109", "Chromium";v="109"
// sec-ch-ua-mobile: ?0
// sec-ch-ua-platform: "Windows"
// Sec-Fetch-Dest: empty
// Sec-Fetch-Mode: cors
// Sec-Fetch-Site: cross-site
// User-Agent: Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/109.0.0.0 Safari/537.36
// 	headers: {
// 		// 'Content-Type': 'application/json',
// 		'Access-Control-Allow-Origin': 'http://localhost:3000',
// 		'Access-Control-Allow-Headers': 'Origin, X-Api-Key, X-Requested-With, Content-Type, Accept, Authorization',
// 		'Access-Control-Allow-Methods': 'POST, PUT, PATCH, GET, DELETE, OPTIONS',
// 		'Access-Control-Allow-Credentials': 'true',
//
// 	}
	headers: {
		'Accept': 'application/json, text/plain, */*',
		'Accept-Encoding': 'gzip, deflate, br',
		'Accept-Language': 'ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7,uk;q=0.6',
		'Access-Control-Allow-Credentials': 'true',
		'Access-Control-Allow-Headers': 'Origin, X-Api-Key, X-Requested-With, Content-Type, Accept, Authorization',
		'Access-Control-Allow-Methods': 'POST, PUT, PATCH, GET, DELETE, OPTIONS',
		'Access-Control-Allow-Origin': 'http://localhost:3000',
		'Connection': 'keep-alive',
		'Host': 'ualib-orion.herokuapp.com',
		'Origin': 'http://localhost:3000',
		'Referer': 'http://localhost:3000/',

	}
});

$api.interceptors.request.use(config => {
	if (AuthService.AccessToken) {
		config.headers.authorization = `Bearer ${AuthService.AccessToken}`;
	}
	return config;
});

$api.interceptors.response.use(
	config => config,
	async error => {
		const originalRequest = error.config;
		if (
			error.response?.status === 401 &&
			error.config &&
			!error.config._isRetry
		) {
			originalRequest._isRetry = true;
			try {
				await axios
					.post(`${BASE_URL}/auth/token/refresh`, {
						refresh: `${localStorage.getItem('refresh')}`,
					})
					.then(res => {
						AuthService.AccessToken = res.data.access;
						localStorage.setItem('refresh', res.data.refresh);
					});
				return $api.request(originalRequest);
			} catch (e) {
				store.dispatch(SetMessage('   ', 'error'));
			}
		} else {
			store.dispatch(SetMessage('', 'error'));
		}
		throw error;
	}
);

export default $api;
